"use client";

import React, { useState, useEffect } from "react";
import { Pencil, Save, X } from "lucide-react";
import { RxCross2 } from "react-icons/rx";
import { API_URL } from "@/src/component/api/apiURL";

/* -------------------------
    SCREENSHOT-LIKE TOGGLE
-------------------------- */
const ToggleSwitch = ({ value, onChange }) => (
  <label className="relative inline-flex cursor-pointer items-center">
    <input
      type="checkbox"
      checked={value}
      onChange={(e) => onChange(e.target.checked)}
      className="peer sr-only"
    />
    <div className="w-11 h-6 bg-gray-300 rounded-full peer-checked:bg-blue-700 transition"></div>
    <div className="absolute left-1 top-[3px] w-5 h-5 bg-white rounded-full transition peer-checked:translate-x-5"></div>
  </label>
);

export default function ProfileCard({ title, apiPath }) {
  const [textInput, setTextInput] = useState("");
  const [selectedState, setSelectedState] = useState("");
  const [stateList, setStateList] = useState([]);
  const [options, setOptions] = useState([]);

  const [editingId, setEditingId] = useState(null);
  const [editText, setEditText] = useState("");
  const [editState, setEditState] = useState("");

  const [error, setError] = useState("");

  const API_URL_DATA = `${API_URL}/api/master/${apiPath}`;

  useEffect(() => {
    fetchOptions();
    if (apiPath === "city") fetchStates();
  }, []);

  const fetchOptions = async () => {
    try {
      const res = await fetch(API_URL_DATA);
      const data = await res.json();
      setOptions(data);
    } catch (err) {
      console.error("Error fetching options:", err);
    }
  };

  const fetchStates = async () => {
    try {
      const res = await fetch(`${API_URL}/api/master/state`);
      const data = await res.json();
      setStateList(data);
    } catch (err) {
      console.error("Error fetching states:", err);
    }
  };

  /* ---------------------- ADD ---------------------- */
  const addOption = async () => {
    const trimmed = textInput.trim();
    if (!trimmed) return;

    const isDuplicate = options.some(
      (opt) => opt.value.trim().toLowerCase() === trimmed.toLowerCase()
    );

    if (isDuplicate) {
      setError("This field already exists");
      return;
    }

    const payload =
      apiPath === "city"
        ? { value: trimmed, state: selectedState }
        : { value: trimmed };

    try {
      const res = await fetch(API_URL_DATA, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const data = await res.json();
      setOptions((prev) => [...prev, data]);
      setTextInput("");
      setSelectedState("");
      setError("");
    } catch (err) {
      console.error("Error adding option:", err);
    }
  };

  /* ---------------------- DELETE ---------------------- */
  const removeOption = async (id) => {
    try {
      await fetch(`${API_URL_DATA}/${id}`, { method: "DELETE" });
      setOptions((prev) => prev.filter((opt) => opt._id !== id));
    } catch (err) {
      console.error("Error deleting:", err);
    }
  };

  /* ---------------------- EDIT ---------------------- */
  const startEdit = (id, value, stateName = "") => {
    setEditingId(id);
    setEditText(value);
    if (apiPath === "city") setEditState(stateName);
  };

  const saveEdit = async (id) => {
    const payload =
      apiPath === "city"
        ? { value: editText, state: editState }
        : { value: editText };

    try {
      const res = await fetch(`${API_URL_DATA}/${id}`, {
        method: "PUT",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      const updated = await res.json();

      setOptions((prev) =>
        prev.map((item) => (item._id === id ? updated : item))
      );

      setEditingId(null);
      setEditText("");
      setEditState("");
    } catch (err) {
      console.error("Error editing:", err);
    }
  };

  return (
    <div className="border border-gray-300 rounded-xl p-5 bg-white w-full max-w-md mt-10 shadow-sm">

      {/* TITLE */}
      <h1 className="text-xl font-semibold text-gray-800 mb-4">{title}</h1>

      {/* CITY STATE SELECT */}
      {apiPath === "city" && (
        <select
          value={selectedState}
          onChange={(e) => setSelectedState(e.target.value)}
          className="p-3 mb-3 border border-gray-300 rounded-lg"
        >
          <option value="">Select State</option>
          {stateList.map((st) => (
            <option key={st._id} value={st.value}>
              {st.value}
            </option>
          ))}
        </select>
      )}

      {/* INPUT */}
      <div className="flex gap-3 mb-4">
        <input
          value={textInput}
          onChange={(e) => {
            setTextInput(e.target.value);
            setError("");
          }}
          placeholder="Enter new value"
          className="w-full p-3 border border-gray-400 rounded-lg"
        />

        <button
          onClick={addOption}
          className="px-5 py-2 bg-blue-700 text-white rounded-lg hover:bg-blue-800"
        >
          Add
        </button>
      </div>

      {error && <p className="text-red-500 text-sm mb-2">{error}</p>}

      {/* LIST */}
      <div className="flex flex-wrap gap-3">
        {options.map((opt) => (
          <div
            key={opt._id}
            className="flex items-center gap-2 px-3 py-2 bg-[#FFE4E4] border border-[#F4C7C7] rounded-lg shadow"
          >
            {editingId === opt._id ? (
              <>
                <input
                  value={editText}
                  onChange={(e) => setEditText(e.target.value)}
                  className="p-1 border border-gray-300 rounded"
                />

                {apiPath === "city" && (
                  <select
                    value={editState}
                    onChange={(e) => setEditState(e.target.value)}
                    className="p-1 border border-gray-300 rounded"
                  >
                    {stateList.map((s) => (
                      <option key={s._id} value={s.value}>
                        {s.value}
                      </option>
                    ))}
                  </select>
                )}

                <button onClick={() => saveEdit(opt._id)}>
                  <Save size={18} className="text-green-700" />
                </button>
                <button onClick={() => setEditingId(null)}>
                  <X size={18} className="text-gray-600" />
                </button>
              </>
            ) : (
              <>
                <span className="font-medium">
                  {opt.value}
                  {opt.state ? ` (${opt.state})` : ""}
                </span>

                {/* TOGGLE FOR TRUE/FALSE VALUES */}
                {typeof opt.value === "boolean" ? (
                  <ToggleSwitch
                    value={opt.value}
                    onChange={(val) => saveEdit(opt._id, val)}
                  />
                ) : null}

                <button
                  onClick={() => startEdit(opt._id, opt.value, opt.state)}
                  className="text-blue-600"
                >
                  <Pencil size={16} />
                </button>

                <button
                  onClick={() => removeOption(opt._id)}
                  className="text-red-700"
                >
                  <RxCross2 size={18} />
                </button>
              </>
            )}
          </div>
        ))}
      </div>
    </div>
  );
}
